//
// Alerts
// --------------------------------------------------
.alert-variant(@background, @border, @text-color, @link-color) {
    background-color: @background;
    border-color: @border;
    color: @text-color;
    hr {
        border-top-color: darken(@border, 5%);
    }
    .alert-link {
        color: @link-color;
    }
}