.progress {
  position: relative;
  z-index: 1;
  height: 6px;
  border-radius: 0;
  box-shadow: none;
}
.progress-bar {
  background-color: #2196F3;
}
.shadow-depth-0 {
  box-shadow: none;
}
.shadow-depth-1 {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
}
.shadow-depth-2 {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
.shadow-depth-3 {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.16), 0 8px 12px rgba(0, 0, 0, 0.19);
}
.shadow-depth-4 {
  box-shadow: 0 9px 15px -1px rgba(0, 0, 0, 0.16), 0 11px 24px -1px rgba(0, 0, 0, 0.23);
}
.shadow-depth-5 {
  box-shadow: 0 13px 25px -2px rgba(0, 0, 0, 0.16), 0 13px 28px -2px rgba(0, 0, 0, 0.23);
}
.ripple-effect {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
}
.ink {
  display: block;
  position: absolute;
  pointer-events: none;
  border-radius: 50%;
  -webkit-transform: scale(0);
  -moz-transform: scale(0);
  -ms-transform: scale(0);
  -o-transform: scale(0);
  transform: scale(0);
  background: #fff;
  opacity: 1;
}
.ink.animate {
  -webkit-animation: ripple .5s linear;
  -moz-animation: ripple .5s linear;
  -ms-animation: ripple .5s linear;
  -o-animation: ripple .5s linear;
  animation: ripple .5s linear;
}
@keyframes ripple {
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}
@-webkit-keyframes ripple {
  100% {
    opacity: 0;
    -webkit-transform: scale(2.5);
    transform: scale(2.5);
  }
}
@-moz-keyframes ripple {
  100% {
    opacity: 0;
    -moz-transform: scale(2.5);
    transform: scale(2.5);
  }
}
@-ms-keyframes ripple {
  100% {
    opacity: 0;
    -ms-transform: scale(2.5);
    transform: scale(2.5);
  }
}
@-o-keyframes ripple {
  100% {
    opacity: 0;
    -o-transform: scale(2.5);
    transform: scale(2.5);
  }
}
.corner-ribbon-wrapper {
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
}
.corner-ribbon {
  width: 200px;
  padding: 10px;
  background: #e43;
  position: absolute;
  text-align: center;
  line-height: 1;
  color: #f0f0f0;
  z-index: 1;
}
/* Custom styles */
.corner-ribbon.shadow {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}
/* Different positions */
.corner-ribbon.top-left {
  top: 100px;
  left: -40px;
  transform: rotate(-45deg);
  transform-origin: top left;
}
.corner-ribbon.top-right {
  top: 100px;
  right: -40px;
  left: auto;
  transform: rotate(45deg);
  transform-origin: top right;
}
.corner-ribbon.bottom-left {
  top: auto;
  bottom: 100px;
  left: -40px;
  transform: rotate(45deg);
  transform-origin: bottom left;
}
.corner-ribbon.bottom-right {
  top: auto;
  right: -40px;
  bottom: 100px;
  left: auto;
  transform: rotate(-45deg);
  transform-origin: bottom right;
}
